<template>
  <div class="about">
    <div class="content">
      <img :src="imgUrl" alt>
      <h6>{{title}}</h6>
    </div>
    <p>{{summary}}</p>
    <Loading v-if="isLoading"></Loading>
  </div>
</template>
<script>
import Loading from "../components/Loading";
import axios from "axios-jsonp-pro";
export default {
  name: "About",
  data() {
    return {
      imgUrl: "",
      title: "",
      summary: "",
      isLoading:true
    }
  },
  components:{
      Loading
  },
  mounted() {
    var id = this.$route.params.id;
    var url = "http://api.douban.com/v2/movie/subject/";
    axios.jsonp(url + id).then(res => {
      this.imgUrl = res.images.small;
      this.title = res.title;
      this.summary = res.summary;
      this.isLoading = false
    });
  }
};
</script>
<style lang="scss" scoped>
.about {
  padding:20px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  h6{
    line-height: 40px;
  }
  img{
    margin-top: 20px;
  }
}
.content{
  text-align: center;
  margin-bottom: 20px;
}
</style>


